body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  margin-top: 125px;
}

.icons {
  margin-top: 50px;
}

.icons button {
  margin-left: 10px;
}
.add-todo .input-field textarea {
  font-size: 64px;
  margin-left: 100px;
  height: 100px;
} 

.add-todo .input-field label {
  font-size: 64px;
  margin-left: 1.5em;
  -webkit-transform: translateY(3px);
          transform: translateY(3px);
}

.add-todo .input-field label.active {
  -webkit-transform: translateY(-28px) scale(0.3);
          transform: translateY(-28px) scale(0.3);
}

.add-todo .input-field i {
  font-size: 82px;
}

.add-todo button {
  margin-left: 10px;
}

